<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }

</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="data" label-width="120px">
            <el-form-item label="公司名称">
                <el-input style="width: 500px" v-model="data.company" placeholder="请输入"></el-input>
            </el-form-item>

            <el-form-item label="企业所处阶段">
                <el-select class="input" v-model="data.stage" placeholder="请选择">
                    <el-option
                            v-for="item in stages"
                            :key="item.value"
                            :label="item.value"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="公司网址">
                <el-input style="width: 500px" v-model="data.website" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="所属行业">
                <el-select class="input" v-model="data.industry" placeholder="请选择">
                    <el-option v-for="item in industries" :key="item.value" :label="item.value"
                               :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="企业法人/负责人">
                <el-input style="width: 500px" v-model="data.legal_person" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="职务">
                <el-input style="width: 500px" v-model="data.duty" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="手机">
                <el-input style="width: 500px" v-model="data.mobile" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="单机座机">
                <el-input style="width: 500px" v-model="data.phone" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="公司邮箱">
                <el-input style="width: 500px" v-model="data.email" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="企业注册地址">
                <el-input style="width: 500px" v-model="data.register_address" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="营业执照编号">
                <el-input style="width: 500px" v-model="data.business_license" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="注册资金">
                <el-input style="width: 500px" v-model="data.register_capital" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="营业执照扫描件"><span
                    style="font-size: 14px;color: #b5b5b5">必须加盖公章 </span>
                <image-select v-model="license_scan" :url="data.license_scan"></image-select>
            </el-form-item>
            <el-form-item label="邮寄地址">
                <el-input style="width: 500px" v-model="data.mailing_address" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="上年度销售额">
                <el-input style="width: 500px" v-model="data.sales_volume" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="公司总人数">
                <el-input style="width: 500px" v-model="data.total_people" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="研发人员数量">
                <el-input style="width: 500px" v-model="data.developer_people" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="专利著作权">
                <el-input style="width: 500px" v-model="data.patent" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="高新技术企业">
                <el-radio-group v-model="data.high_technology">
                    <el-radio-button label="0">否</el-radio-button>
                    <el-radio-button label="1">是</el-radio-button>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="期望服务方向">
                <el-select class="input" v-model="data.service_direction" placeholder="请选择">
                    <el-option
                            v-for="item in service"
                            :key="item.value"
                            :label="item.value"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="主要产品介绍">
                <el-input class="input" style="width:500px" :autosize="{ minRows: 5 }" type="textarea"
                          v-model="data.products_introduce"
                          placeholder="请输入"/>
            </el-form-item>
            <el-form-item label="主营业务介绍">
                <el-input class="input" style="width:500px" :autosize="{ minRows: 5 }" type="textarea"
                          v-model="data.business_introduce"
                          placeholder="请输入"/>
            </el-form-item>
            <el-form-item label="公司logo">
                <image-select v-model="logo" :url="data.logo"></image-select>
            </el-form-item>
            <el-form-item style="margin-top: 100px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

    import api from '../../js/api/member/review'
    import baseApi from '../../js/api/baseApi'

    export default {
        name: "userInfo",
        data() {
            return {
                user: {},
                disable: true,
                userData: {},
                stages: [{value: '初创'}, {value: '首轮融资'}, {value: '二轮融资'}, {value: '三轮融资'}, {value: '准备上市'}, {value: '创业板上市'}, {value: '中小板上市'}, {value: '主板上市'}, {value: '国外上市'}],
                value: null,
                role: [],
                industries: [{value: '硬件'}, {value: '软件'}, {value: '应用'}, {value: '服务'}, {value: '其他'}],
                service: [{value: '银行贷款'}, {value: '股权融资'}, {value: '新三板咨询与培训'}, {value: '政府扶持资金'}, {value: '应用示范工程'}, {value: '企业沙龙论坛 '}, {value: '专利技术申请'}, {value: '高新企业申请'}, {value: '其他'}],
                data: {high_technology: 0},
                logo: null,
                license_scan: null,
                isLoading: false,

            }
        },

        mounted() {
            if (this.$route.query.id !== undefined) {
                this.loadData()
            }
            // this.role = type.role;
            // this.disable = false;
            // this.industry = type.industry;
        },
        methods: {
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.data = res.data.data;
                }, () => {
                })

            },
            editEd() {
                this.disable = false;
            },
            onSubmit() {
                if (!baseApi.isStrValid(this.data.company)) {
                    this.$message.error('请输入公司名称');
                    return;
                }
                if (!baseApi.isStrValid(this.data.stage)) {
                    this.$message.error('请选择企业所处阶段');
                    return;
                }
                if (!baseApi.isStrValid(this.data.website)) {
                    this.$message.error('请输入公司网址');
                    return;
                }
                if (!baseApi.isStrValid(this.data.industry)) {
                    this.$message.error('请选择所属行业');
                    return;
                }
                if (!baseApi.isStrValid(this.data.legal_person)) {
                    this.$message.error('请输入企业法人/负责人');
                    return;
                }
                if (!baseApi.isStrValid(this.data.duty)) {
                    this.$message.error('请输入职务');
                    return;
                }
                if (!baseApi.isStrValid(this.data.mobile)) {
                    this.$message.error('请输入手机');
                    return;
                }
                if (!baseApi.isStrValid(this.data.phone)) {
                    this.$message.error('请输入单位座机');
                    return;
                }
                if (!baseApi.isStrValid(this.data.email)) {
                    this.$message.error('请输入公司邮箱');
                    return;
                }
                if (!baseApi.isStrValid(this.data.register_address)) {
                    this.$message.error('请输入企业注册地址');
                    return;
                }
                if (!baseApi.isStrValid(this.data.business_license)) {
                    this.$message.error('请输入营业执照编号');
                    return;
                }
                if (!baseApi.isStrValid(this.data.register_capital)) {
                    this.$message.error('请输入注册资金');
                    return;
                }
                if (this.license_scan == null && !baseApi.isStrValid(this.data.license_scan)) {
                    this.$message.error('请选择营业执照扫描件');
                    return;
                }
                if (!baseApi.isStrValid(this.data.mailing_address)) {
                    this.$message.error('请输入邮寄地址');
                    return;
                }
                if (!baseApi.isStrValid(this.data.sales_volume)) {
                    this.$message.error('请输入上年度营销额');
                    return;
                }
                if (!baseApi.isStrValid(this.data.total_people)) {
                    this.$message.error('请输入公司总人数');
                    return;
                }
                if (!baseApi.isStrValid(this.data.developer_people)) {
                    this.$message.error('请输入研发人员数量');
                    return;
                }
                if (!baseApi.isStrValid(this.data.patent)) {
                    this.$message.error('请输入专利及著作权');
                    return;
                }
                if (!baseApi.isStrValid(this.data.high_technology)) {
                    this.$message.error('请输入高新技术企业');
                    return;
                }
                if (!baseApi.isStrValid(this.data.service_direction)) {
                    this.$message.error('请选择期望服务方向');
                    return;
                }
                if (!baseApi.isStrValid(this.data.products_introduce)) {
                    this.$message.error('请输入主要产品介绍');
                    return;
                }
                if (!baseApi.isStrValid(this.data.business_introduce)) {
                    this.$message.error('请输入主营业务介绍'
                    );
                    return;
                }
                if (this.logo == null && !baseApi.isStrValid(this.data.logo)) {
                    this.$message.error('请选择公司logo');
                    return;
                }
                let data = baseApi.objToFormData(this.data);
                data.append('logo', this.logo);
                data.append('license_scan', this.license_scan);

                api.save(data, () => {
                    this.$message.success('保存成功');
                    this.disable = true;
                    this.$router.push({path: 'reviewMember'});
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })
            },

            back() {
                this.$router.back();
            },
        }
    }
</script>
